import React from 'react';

/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { layout } from '@wordpress/icons';
import { useDispatch, useSelect } from '@wordpress/data';
import {
	ToolbarDropdownMenu,
	MenuGroup,
	MenuItem,
	SelectControl
} from '@wordpress/components';
import { useBlockEditContext } from '@wordpress/block-editor';

/**
 * Custom Dependencies
 */
import { AVAILABLE_LAYOUTS } from '../../constants';
import PropTypes from 'prop-types';

function BlockLayoutSelector ( props ) {
	const { clientId } = useBlockEditContext();
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const attributes = useSelect( ( select ) => {
		return select( 'core/block-editor' ).getBlockAttributes( clientId );
	} );
	const setAttributes = ( newAttributes ) =>
		updateBlockAttributes( clientId, newAttributes );

	const availableLayouts = AVAILABLE_LAYOUTS.filter(
		( layout ) => !props.exclude.includes( layout.slug )
	);

	return (
		props.inToolbar ? <ToolbarDropdownMenu
			icon={ layout }
			label={ __( 'Layout', 'gutenberghub-query-taxonomy' ) }
			toggleProps={ {
				describedBy: __( 'Change Layout' ),
			} }
			children={ ( onClose ) => {
				return (
					<MenuGroup>
						{ availableLayouts.map( ( layout, idx ) => {
							const isActive = attributes.layout === layout.slug;

							return (
								<MenuItem
									key={ idx }
									iconPosition="left"
									isPressed={ isActive }
									onClick={ () =>
										setAttributes( {
											layout: layout.slug,
										} )
									}
								>
									{ layout.label }
								</MenuItem>
							);
						} ) }
					</MenuGroup>
				);
			} }
		/> : <SelectControl
			options={ availableLayouts }
			onChange={ ( newLayout ) =>
				setAttributes( {
					layout: newLayout,
				} )
			}
			value={ attributes.layout }
			label={ __( 'Display as', 'gutenberghub-query-taxonomy' ) }
			help={ __(
				'Choose different available layouts to render the taxonomy filter as.',
				'gutenberghub-query-taxonomy'
			) }
		/>
	);
}

BlockLayoutSelector.defaultProps = {
	exclude: [],
};

BlockLayoutSelector.propTypes = {
	/** Layout to exclude */
	exclude: PropTypes.array,
	/** True if select layout should show in toolbar */
	inToolbar: PropTypes.bool.isRequired
};

export default BlockLayoutSelector;
