/**
 * WordPress dependencies
 */
import { isEmpty } from 'lodash';
import { useSelect } from '@wordpress/data';
import { useEffect, useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { store as coreStore } from '@wordpress/core-data';
import { __experimentalHasSplitBorders as hasSplitBorders } from '@wordpress/components';

/**
 * Hook that returns the taxonomies associated with a specific post type.
 *
 * @param {string} postType The post type from which to retrieve the associated taxonomies.
 * @return {Object[]} An array of the associated taxonomies.
 */
export const useTaxonomies = ( postType ) => {
	const taxonomies = useSelect(
		( select ) => {
			const { getTaxonomies } = select( coreStore );
			const filteredTaxonomies = getTaxonomies( {
				type: postType,
				per_page: -1,
				context: 'view',
			} );
			return filteredTaxonomies;
		},
		[ postType ]
	);
	return taxonomies;
};

/**
 * Hook that provides the taxonomy terms associated with a specific taxonomy.
 *
 * @param {string} slug The taxonomy slug
 * @param {number[]|-1} includeTermIds Term ids to include, will include all terms if -1.
 * @param {boolean} hideEmpty Weather to hide terms not assigned to any posts.
 *
 * @return {{ terms: Object[]; isResolved: boolean; }} An array of associated taxonomy terms.
 */
export const useTaxonomyTerms = (
	slug,
	includeTermIds,
	hideEmpty,
	postType
) => {
	const { terms, isResolved, totalCount } = useSelect(
		( select ) => {
			const { getEntityRecords, hasFinishedResolution } = select(
				coreStore
			);

			const query = {
				order: 'asc',
				_fields: 'id,name,count',
				context: 'view',
				orderby: 'name',
				per_page: -1,
				hide_empty: hideEmpty,
				count: true,
			};

			if ( includeTermIds !== -1 ) {
				query.include = includeTermIds;
			}

			const selectorArgs = [ 'taxonomy', slug, query ];

			const totalQuery = {
				per_page: -1,
			};
			const totalArgs = [ 'postType', postType, totalQuery ];
			const totalEntities = getEntityRecords( ...totalArgs );

			return {
				terms: getEntityRecords( ...selectorArgs ),
				isResolved: hasFinishedResolution(
					'getEntityRecords',
					selectorArgs
				),
				totalCount: ! isEmpty( totalEntities )
					? totalEntities.length
					: 0,
			};
		},
		[ slug, includeTermIds, hideEmpty, postType ]
	);

	return { terms, isResolved, totalCount };
};

/**
 * Hook that provides the total count.
 *
 * @param {string} slug The postType slug
 * @param {boolean} hideEmpty Weather to hide terms not assigned to any posts.
 *
 * @return {{ totalCount: number; isLoading: boolean;  }}
 */
export const useTotalCount = ( postType ) => {
	const [ isLoading, setLoading ] = useState( false );
	const [ totalCount, setTotalCount ] = useState( 0 );

	const restBaseName = useSelect( ( select ) => {
		return select( 'core' ).getPostType( postType )?.rest_base;
	} );

	useEffect( () => {
		setLoading( true );

		if ( restBaseName ) {
			apiFetch( {
				path: `/wp/v2/${ restBaseName }?per_page=1&page=1`,
				parse: false,
			} )
				.then( ( response ) => {
					setLoading( false );
					setTotalCount(
						Number( response.headers.get( 'X-WP-Total' ) )
					);
				} )
				.catch( ( error ) => {
					console.error( error );
				} );
		}
	}, [ restBaseName ] );

	return { totalCount, isLoading };
};

/**
 * Function that's help you to generate splitted or non splitted border CSS.
 * @param {object} object border attributes
 * 
 * @return {{ css:object }} A css object
 */
export const getBorderCSS = ( object ) => {
    let css = {};

    if ( !hasSplitBorders( object ) ) {
        css[ 'top' ] = object;
        css[ 'right' ] = object;
        css[ 'bottom' ] = object;
        css[ 'left' ] = object;
        return css;
    }
    return object;
};
