/**
 * WordPress Dependencies
 */
import React from 'react';
import { __ } from '@wordpress/i18n';
import { isEmpty } from 'lodash';
import { BlockIcon, useBlockEditContext } from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { Placeholder, SelectControl, Button } from '@wordpress/components';

/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';
import { useTaxonomies } from '../../utils';
import { filter } from '../../icons';

/**
 * Placeholder.
 *
 * @param {{
 *  postType: string;
 *  onSelect: (taxonomy: string) => void
 * }} props
 */
function QueryTaxonomyPlaceholder( props ) {
	const [ selectedTaxonomy, setSelectedTaxonomy ] = useState( '' );
	const { clientId } = useBlockEditContext();

	const taxonomies = useTaxonomies( props.postType );

	const attributes = useSelect( ( select ) =>
		select( 'core/block-editor' ).getBlockAttributes( clientId )
	);

	const instructions = __(
		'Select a parent taxonomy you want to filter results with.',
		'gutenberghub-query-taxonomy'
	);

	const hasTaxonomies = Array.isArray( taxonomies );
	const taxonomiesMap = taxonomies?.map( ( taxonomy ) => {
		return {
			label: taxonomy.name,
			value: taxonomy.slug,
		};
	} );

	return props?.inInspectorControl === true ? (
		<SelectControl
			value={ attributes?.taxonomySlug }
			label={ __( 'Query Filter', 'gutenberghub-query-taxonomy' ) }
			options={ taxonomiesMap }
			onChange={ ( selectedTaxonomy ) =>
				props.onSelect( selectedTaxonomy )
			}
			__nextHasNoMarginBottom
		/>
	) : (
		<Placeholder
			instructions={ instructions }
			icon={ <BlockIcon icon={ filter } /> }
			label={ __( 'Query Filter', 'gutenberghub-query-taxonomy' ) }
		>
			{ hasTaxonomies && (
				<>
					<div className="gutenberghub-taxonomy-group">
						{ taxonomies.map( ( taxonomy ) => (
							<label
								className="gutenberghub-taxonomy-item"
								onClick={ () =>
									setSelectedTaxonomy( taxonomy.slug )
								}
							>
								<input
									type="radio"
									value={ selectedTaxonomy }
									name="select-taxonomy"
								/>
								<span>{ taxonomy?.name }</span>
							</label>
						) ) }
					</div>
					<Button
						variant="primary"
						key={ selectedTaxonomy }
						disabled={ isEmpty( selectedTaxonomy ) }
						onClick={ () => props.onSelect( selectedTaxonomy ) }
					>
						{ __( 'Done', 'gutenberghub-query-taxonomy' ) }
					</Button>
				</>
			) }
		</Placeholder>
	);
}

QueryTaxonomyPlaceholder.propTypes = {
	/**
	 * Post type slug.
	 */
	postType: PropTypes.string.isRequired,

	/**
	 * On Taxonomy Selection.
	 */
	onSelect: PropTypes.func.isRequired,
};

export default QueryTaxonomyPlaceholder;
