/**
 * WordPress Dependencies
 */
import React from 'react';

import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';
import {
	PanelBody,
	TextControl,
	ToggleControl,
	__experimentalUnitControl as UnitControl,
} from '@wordpress/components';

/**
 * Custom Dependencies
 */
import {
	AVAILABLE_JUSTIFICATIONS,
	AVAILABLE_ORIENTATION,
} from '../../constants';
import {
	BlockLayoutSelector,
	BorderControl,
	ColorSettings,
	CustomToggleGroupControl,
	DimensionControl,
} from '../../components';
import QueryTaxonomyPlaceholder from './placeholder';

function Inspector( props ) {
	const { setAttributes, attributes } = props;
	const skipBlockForOrientationSupport = attributes.layout !== 'select';

	const { defaultColors, defaultGradients } = useSelect( ( select ) => {
		return {
			defaultColors: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.palette?.default,
			defaultGradients: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.gradients?.default,
		};
	} );

	if ( attributes.orientation === 'column' ) {
		AVAILABLE_JUSTIFICATIONS.slice(
			0,
			AVAILABLE_JUSTIFICATIONS.length - 1
		);
	}

	return (
		<>
			<InspectorControls>
				<PanelBody>
					{ props.attributes.taxonomySlug !== '' && (
						<QueryTaxonomyPlaceholder
							inInspectorControl
							onSelect={ ( taxonomySlug ) =>
								props.setAttributes( { taxonomySlug } )
							}
							postType={ props.context.query.postType }
						/>
					) }
					<ToggleControl
						checked={ attributes.hideEmpties }
						label={ __(
							'Hide Empty',
							'gutenberghub-query-taxonomy'
						) }
						onChange={ () => {
							props.setAttributes( {
								hideEmpties: ! props.attributes.hideEmpties,
							} );
						} }
						help={ __(
							'Hide the terms that does not have any posts assigned to it.'
						) }
					/>
					<ToggleControl
						checked={ attributes.displayCount }
						label={ __(
							'Total Count',
							'gutenberghub-query-taxonomy'
						) }
						onChange={ () => {
							props.setAttributes( {
								displayCount: ! props.attributes.displayCount,
							} );
						} }
						help={ __(
							'Displays the total post count assigned to the taxonomy.'
						) }
					/>
					{ props.attributes.layout !== 'checkbox' && (
						<>
							<ToggleControl
								checked={ attributes.showAll }
								label={ __(
									'Show All Label',
									'gutenberghub-query-taxonomy'
								) }
								onChange={ () => {
									props.setAttributes( {
										showAll: ! props.attributes.showAll,
									} );
								} }
								help={ __(
									'Displays All label and show all query result when selected.',
									'gutenberghub-query-taxonomy'
								) }
							/>
							{ props.attributes.showAll && (
								<TextControl
									label={ __(
										'Label',
										'gutenberghub-query-taxonomy'
									) }
									placeholder={ __(
										'All',
										'gutenberghub-query-taxonomy'
									) }
									onChange={ ( showAllLabel ) =>
										props.setAttributes( { showAllLabel } )
									}
									value={ props.attributes.showAllLabel }
								/>
							) }
						</>
					) }
				</PanelBody>
				<PanelBody
					title={ __( 'Layout', 'gutenberghub-query-taxonomy' ) }
				>
					<BlockLayoutSelector inToolbar={ false } />
					{ skipBlockForOrientationSupport && (
						<div className="ghub-justification-control">
							<CustomToggleGroupControl
								options={ AVAILABLE_JUSTIFICATIONS }
								attributeKey="justification"
								label={ __(
									'Justification',
									'gutenberghub-query-taxonomy'
								) }
							/>
							<CustomToggleGroupControl
								options={ AVAILABLE_ORIENTATION }
								attributeKey="orientation"
								label={ __(
									'Orientation',
									'gutenberghub-query-taxonomy'
								) }
							/>
						</div>
					) }
					{ skipBlockForOrientationSupport &&
						attributes.orientation === 'column' && (
							<UnitControl
								label={ __(
									'Height',
									'gutenberghub-query-taxonomy'
								) }
								value={ attributes.height }
								onChange={ ( newValue ) => {
									props.setAttributes( { height: newValue } );
								} }
							/>
						) }
				</PanelBody>
			</InspectorControls>

			{ attributes.layout === 'menu' && (
				<>
					<InspectorControls group="color">
						<ColorSettings labelPrefix="Button" />
					</InspectorControls>
					<InspectorControls group="dimensions">
						<DimensionControl
							inputProps={ { min: -Infinity } }
							value={ {
								padding: attributes?.buttonPadding,
								buttonGap: attributes?.buttonGap,
							} }
							onChange={ ( value, type ) => {
								if ( type === 'padding' ) {
									props.setAttributes( {
										buttonPadding: value,
									} );
								} else if ( type === 'buttonGap' ) {
									props.setAttributes( { buttonGap: value } );
								}
							} }
							onReset={ ( type ) => {
								if ( type === 'padding' ) {
									props.setAttributes( {
										buttonPadding: {},
									} );
								} else if ( type === 'buttonGap' ) {
									props.setAttributes( { buttonGap: {} } );
								}
							} }
						/>
					</InspectorControls>
					<InspectorControls group="border">
						<BorderControl
							colors={ defaultColors }
							borderValue={ attributes.border }
							borderRadiusValue={ attributes.borderRadius }
							onBorderChange={ ( newBorder ) =>
								setAttributes( { border: newBorder } )
							}
							onBorderReset={ () =>
								setAttributes( { border: {} } )
							}
							onBorderRadiusReset={ () =>
								setAttributes( { borderRadius: {} } )
							}
							onBorderRadiusChange={ ( newBorderRadius ) =>
								setAttributes( {
									borderRadius: newBorderRadius,
								} )
							}
						/>
					</InspectorControls>
				</>
			) }
		</>
	);
}

export default Inspector;
