/**
 * WordPress Dependencies
 */

import { useDispatch, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';
import {
	useBlockEditContext,
	__experimentalColorGradientSettingsDropdown as ColorGradientSettingsDropdown,
	__experimentalUseMultipleOriginColorsAndGradients as useMultipleOriginColorsAndGradients,
} from '@wordpress/block-editor';

/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';

function ColorSettings( props ) {
	const { labelPrefix } = props;
	const { clientId } = useBlockEditContext();
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const attributes = useSelect( ( select ) => {
		return select( 'core/block-editor' ).getBlockAttributes( clientId );
	} );
	const setAttributes = ( newAttributes ) =>
		updateBlockAttributes( clientId, newAttributes );
	const colorGradientSettings = useMultipleOriginColorsAndGradients();
	const { defaultColors, defaultGradients } = useSelect( ( select ) => {
		return {
			defaultColors: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.palette?.default,
			themeColors: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.palette?.theme,
			defaultGradients: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.gradients?.default,
			themeGradients: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.gradients?.theme,
		};
	} );

	return (
		<ColorGradientSettingsDropdown
			{ ...colorGradientSettings }
			panelId={ clientId }
			title={ __( 'Color Settings', 'gutenberghub-query-sort' ) }
			popoverProps={ {
				placement: 'left start',
			} }
			settings={ [
				{
					colorValue: attributes.textColor,
					colors: defaultColors,
					label: sprintf(
						__( '%1$s Text Color', 'gutenberghub-query-sort' ),
						labelPrefix
					),
					onColorChange: ( newValue ) =>
						setAttributes( { textColor: newValue } ),
				},
				{
					colorValue: attributes.activeTextColor,
					colors: defaultColors,
					label: sprintf(
						__(
							'Active %1$s Text Color',
							'gutenberghub-query-sort'
						),
						labelPrefix
					),
					onColorChange: ( newValue ) =>
						setAttributes( { activeTextColor: newValue } ),
				},
				{
					colorValue: attributes.background,
					gradientValue: attributes.gradientBackground,
					colors: defaultColors,
					gradients: defaultGradients,
					label: sprintf(
						__( '%1$s Background', 'gutenberghub-query-sort' ),
						labelPrefix
					),
					onColorChange: ( newValue ) =>
						setAttributes( { background: newValue } ),
					onGradientChange: ( newValue ) =>
						setAttributes( { gradientBackground: newValue } ),
				},
				{
					colorValue: attributes.activeBackground,
					gradientValue: attributes.activeGradientBackground,
					colors: defaultColors,
					gradients: defaultGradients,
					label: sprintf(
						__(
							'Active %1$s Background',
							'gutenberghub-query-sort'
						),
						labelPrefix
					),
					onColorChange: ( newValue ) => {
						setAttributes( { activeBackground: newValue } );
					},
					onGradientChange: ( newValue ) => {
						setAttributes( { activeGradientBackground: newValue } );
					},
				},
			] }
		/>
	);
}

ColorSettings.propTypes = {
	/**Add add prefix on label*/
	labelPrefix: PropTypes.string.isRequired,
};
export default ColorSettings;
