import React from 'react';

/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';

function SelectLayout( props ) {
	return (
		<>
			<select>
				{ props?.showAll && (
					<option value="">
						{ props?.showAllLabel }
						{ props?.displayCount
							? ` (${ props.totalCount })`
							: '' }
					</option>
				) }
				{ props?.items?.map( ( item ) => (
					<option value={ item.slug }>{ `${ item.name }${
						props?.displayCount ? ` (${ item.count })` : ''
					}` }</option>
				) ) }
			</select>
		</>
	);
}

SelectLayout.defaultProps = {
	showAll: true,
};

SelectLayout.propTypes = {
	showAll: PropTypes.bool,
};

export default SelectLayout;
