import { getBorderCSS } from '../../utils';
import { isEmpty, omitBy, isUndefined, trim } from 'lodash';

export function getStyles( attributes ) {
	const height =
		attributes.orientation === 'column' && attributes.layout !== 'select'
			? attributes.height
			: '';
	const paddingCSS = attributes.buttonPadding;
	const borderRadiusCSS = attributes.borderRadius;
	const borderCSS = getBorderCSS( attributes.border );

	let styles = {
		'--ghub-qbt-height': height,
		'--ghub-qbt-button-color': attributes?.textColor,
		'--ghub-qbt-button-active-color': attributes?.activeTextColor,
		'--ghub-qbt-button-bg-color': attributes?.background,
		'--ghub-qbt-button-gradient-color': attributes?.gradientBackground,
		'--ghub-qbt-button-active-bg-color': attributes?.activeBackground,
		'--ghub-qbt-button-active-gradient-color':
			attributes?.activeGradientBackground,
		'--ghub-qbt-button-padding-top': paddingCSS?.top,
		'--ghub-qbt-button-padding-right': paddingCSS?.right,
		'--ghub-qbt-button-padding-bottom': paddingCSS?.bottom,
		'--ghub-qbt-button-padding-left': paddingCSS?.left,
		'--ghub-qbs-button-gap': attributes.buttonGap,
		'--ghub-qbt-button-top-left-radius': borderRadiusCSS?.top,
		'--ghub-qbt-button-top-right-radius': borderRadiusCSS?.right,
		'--ghub-qbt-button-bottom-left-radius': borderRadiusCSS?.bottom,
		'--ghub-qbt-button-bottom-right-radius': borderRadiusCSS?.left,
		'--ghub-qbt-button-border-top': `${ borderCSS?.top?.width ?? '' } ${
            borderCSS?.top?.width && isEmpty( borderCSS?.top?.style )
                ? 'solid'
                : borderCSS?.top?.style ?? ''
        } ${ borderCSS?.top?.color ?? '' }`,
        '--ghub-qbt-button-border-right': `${ borderCSS?.right?.width ?? '' } ${
            borderCSS?.right?.width && isEmpty( borderCSS?.right?.style )
                ? 'solid'
                : borderCSS?.right?.style ?? ''
        } ${ borderCSS?.right?.color ?? '' }`,
        '--ghub-qbt-button-border-bottom': `${
            borderCSS?.bottom?.width ?? ''
        } ${
            borderCSS?.bottom?.width && isEmpty( borderCSS?.bottom?.style )
                ? 'solid'
                : borderCSS?.bottom?.style ?? ''
        } ${ borderCSS?.bottom?.color ?? '' }`,
        '--ghub-qbt-button-border-left': `${ borderCSS?.left?.width ?? '' } ${
            borderCSS?.left?.width && isEmpty( borderCSS?.left?.style )
                ? 'solid'
                : borderCSS?.left?.style ?? ''
        } ${ borderCSS?.left?.color ?? '' }`,
	};

	return omitBy( styles, ( value ) => {
		return (
			isUndefined( value ) ||
			value === false ||
			trim( value ) === 'undefined undefined undefined' ||
			isEmpty( value )
		);
	} );
}


export function getStylesClasses( attributes ) {

	 const isValueEmpty = ( value ) => {
        return (
            isUndefined( value ) ||
            value === false ||
            trim( value ) === '' ||
            trim( value ) === 'undefined undefined undefined' ||
            isEmpty( value )
        );
    };

	return {
		'has-ghub-text-color': ! isValueEmpty( attributes.textColor ),
		'has-ghub-active-text-color': ! isValueEmpty( attributes.activeTextColor ),
		'has-ghub-active-background-color': ! isValueEmpty( attributes.activeBackground ) ||  ! isValueEmpty( attributes.activeGradientBackground ),
		'has-ghub-background-color': ! isValueEmpty( attributes.background ) || ! isValueEmpty( attributes.gradientBackground ),
		'has-ghub-padding': ! isValueEmpty( attributes.buttonPadding ),
		'has-ghub-border-radius': ! isValueEmpty( attributes.borderRadius ),
		'has-ghub-border': ! isValueEmpty( attributes.border ),
		'has-ghub-margin': ! isValueEmpty( attributes.buttonMargin ),
	}
}
