import React from 'react';

function RadioLayout( props ) {
	return (
		<>
			<div className="gutenberghub-query-taxonomy-radio">
				{ props.showAll && (
					<label>
						<input
							type="radio"
							name={ props.name }
							value={ '' }
							checked={ true }
						/>
						{ props?.showAllLabel }
						{ props?.displayCount
							? ` (${ props.totalCount })`
							: '' }
					</label>
				) }
				{ props?.items?.map( ( item, index ) => {
					return (
						<>
							<label>
								<input
									type="radio"
									name={ props.name }
									value={ item.slug }
								/>
								{ `${ item.name }${
									props?.displayCount
										? ` (${ item.count })`
										: ''
								}` }
							</label>
						</>
					);
				} ) }
			</div>
		</>
	);
}

export default RadioLayout;
