import React from 'react';

/**
 * WordPress Dependencies
 */
import PropTypes from 'prop-types';

function MenuLayout( props ) {
	return (
		<>
			<div className="gutenberghub-query-taxonomy-menu wp-block-buttons">
				{ props.showAll && (
					<a className="is-active-menu wp-block-button wp-element-button">
						{ props?.showAllLabel }
						{ props?.displayCount
							? ` (${ props.totalCount })`
							: '' }
					</a>
				) }
				{ props?.items?.map( ( item, index ) => {
					return (
						<a
							data-slug={ item.slug }
							className={ `wp-block-button wp-element-button ${
								! props.showAll && index === 0
									? ' is-active-menu'
									: ''
							}` }
							key={ item.slug }
						>
							{ `${ item.name }${
								props?.displayCount ? ` (${ item.count })` : ''
							}` }
						</a>
					);
				} ) }
			</div>
		</>
	);
}

MenuLayout.defaultProps = {
	showAll: true,
};

MenuLayout.propTypes = {
	showAll: PropTypes.bool,
};

export default MenuLayout;
