import React from 'react';
import { useBlockProps } from '@wordpress/block-editor';

import { isEmpty } from 'lodash';
import classNames from 'classnames';

import Inspector from './inspector';
import BlockControls from './block-controls';
import TaxonomyMap from './taxonomy-map';
import QueryTaxonomyPlaceholder from './placeholder';
import { getStyles, getStylesClasses } from './get-styles';

function edit( props ) {
	const blockProps = useBlockProps( {
		className: classNames( 'gutenberghub-query-taxonomy has-ghub-layout', {
			[ `justify-content-${ props.attributes.justification }` ]: ! isEmpty(
				props.attributes.justification
			),
			[ `flex-direction-${ props.attributes.orientation }` ]: ! isEmpty(
				props.attributes.orientation
			),
			...getStylesClasses(props.attributes)
		} ),
		style: getStyles( props.attributes ),
	} );

	const taxQuery = props.context.query?.taxQuery;
	const hasSelectedTaxonomy = ! isEmpty( props.attributes.taxonomySlug );
	const postType = props.context.query.postType;

	return (
		<>
			<div { ...blockProps }>
				{ ! hasSelectedTaxonomy ? (
					<QueryTaxonomyPlaceholder
						onSelect={ ( taxonomySlug ) =>
							props.setAttributes( { taxonomySlug } )
						}
						postType={ postType }
					/>
				) : (
					<TaxonomyMap
						postType={ postType }
						taxQuery={ taxQuery }
						layout={ props.attributes.layout }
						showAll={ props.attributes.showAll }
						slug={ props.attributes.taxonomySlug }
						hideEmpty={ props.attributes.hideEmpties }
						displayCount={ props.attributes.displayCount }
						showAllLabel={ props.attributes.showAllLabel }
						onChange={ ( newLabel ) =>
							props.setAttributes( { label: newLabel } )
						}
					/>
				) }
			</div>
			<Inspector { ...props } />
			<BlockControls { ...props } />
		</>
	);
}

export default edit;
