import React from 'react';

/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';
import { useTaxonomyTerms, useTotalCount } from '../../utils';

import Menu from '../../layouts/menu';
import Select from '../../layouts/select';
import Checkbox from '../../layouts/checkbox';
import RadioLayout from '../../layouts/radio';

/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { isEmpty, get } from 'lodash';
import { Spinner, Placeholder } from '@wordpress/components';

/**
 * Taxonomy terms map.
 *
 * @param {{ slug: string; taxQuery?: object; layout: "checkbox" | "radio" | "select" | "menu"; hideEmpty: boolean; }} props
 * @returns
 */
function TaxonomyMap( props ) {
	const hasTaxonomyQuery =
		! isEmpty( props.taxQuery ) &&
		get( props.taxQuery, [ props.slug, 'length' ] ) > 0;

	const includeTermIds = hasTaxonomyQuery
		? get( props.taxQuery, props.slug, -1 )
		: -1;

	const { totalCount, isLoading } = useTotalCount( props.postType );

	const { terms, isResolved } = useTaxonomyTerms(
		props.slug,
		includeTermIds,
		props.hideEmpty,
		props.postType
	);

	if ( ! isResolved && isLoading ) {
		return <Spinner />;
	}

	if ( isResolved && terms?.length === 0 ) {
		return (
			<Placeholder
				label={ __( 'No Terms found', 'gutenberghub-query-taxonomy' ) }
				instructions={ __(
					'You have selected a taxonomy that does not have any terms. Please try again after creating some terms in the selected taxonomy.'
				) }
			/>
		);
	}

	const termPickers = {
		select: Select,
		menu: Menu,
		checkbox: Checkbox,
		radio: RadioLayout,
	};

	const TaxonomyTermPicker =
		props.layout in termPickers ? termPickers[ props.layout ] : () => null;

	return (
		<TaxonomyTermPicker
			items={ terms }
			name="taxonomy-filter"
			totalCount={ totalCount ?? 0 }
			showAll={ props.showAll }
			onChange={ props.onChange }
			showAllLabel={ isEmpty(props.showAllLabel) ? "All" : props.showAllLabel }
			displayCount={ props.displayCount }
		/>
	);
}

TaxonomyMap.propTypes = {
	/**
	 *  True if the user check it.
	 */
	hideMainLabel: PropTypes.bool.isRequired,

	/**
	 *  onChange function change the label value
	 */
	onChange: PropTypes.func.isRequired,

	/**
	 *  Label value
	 */
	value: PropTypes.string.isRequired,

	/**
	 * Taxonomy Slug.
	 */
	slug: PropTypes.string.isRequired,

	/**
	 * Taxonomy Query.
	 */
	taxQuery: PropTypes.any,

	/**
	 * Should hide empty terms?.
	 */
	hideEmpty: PropTypes.bool,

	/**
	 * Post type name
	 */
	postType: PropTypes.string.isRequired,
};

export default TaxonomyMap;
