import React from 'react';

function CheckboxLayout( props ) {
	return (
		<>
			<div className="gutenberghub-query-taxonomy-checkbox">
				{ props?.items?.map( ( item ) => {
					return (
						<>
							<label>
								<input
									type="checkbox"
									name={ item.slug }
									value={ item.slug }
								/>
								{ `${ item.name }${
									props?.displayCount
										? ` (${ item.count })`
										: ''
								}` }
							</label>
						</>
					);
				} ) }
			</div>
		</>
	);
}

export default CheckboxLayout;
