import React from 'react';

/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import {
	JustifyContentControl,
	BlockControls as WPBlockControls,
} from '@wordpress/block-editor';
import { ToolbarGroup } from '@wordpress/components';

/**
 * Custom Dependencies
 */
import { BlockLayoutSelector } from '../../components';

function BlockControls ( props ) {
	const justificationSupportedLayouts = [ 'menu', 'checkbox', 'radio' ];
	const isJustificationSupported = justificationSupportedLayouts.includes(
		props.attributes.layout
	);

	return (
		<WPBlockControls>
			<ToolbarGroup>
				{ isJustificationSupported && (
					<JustifyContentControl
						value={ props.attributes.justification }
						onChange={ ( nextJustification ) => {
							props.setAttributes( {
								justification: nextJustification,
							} );
						} }
					/>
				) }

				<BlockLayoutSelector inToolbar={ true } />
			</ToolbarGroup>
		</WPBlockControls>
	);
}

export default BlockControls;
