/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { useBlockEditContext } from '@wordpress/block-editor';
import {
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalBoxControl as WPBoxControl,
	__experimentalUnitControl as UnitControl,
} from '@wordpress/components';

/**
 * Custom Dependencies
 */

function DimensionControl( props ) {
	const { clientId } = useBlockEditContext();

	return (
		<>
			{ ! props.excludePadding && (
				<ToolsPanelItem
					panelId={ clientId }
					hasValue={ () => !! props.value.padding }
					label="Button Padding"
					onDeselect={ () => props.onReset( 'padding' ) }
				>
					<WPBoxControl
						{ ...props }
						label="Button Padding"
						resetValues={ {} }
						values={ props.value.padding }
						onChange={ ( newValue ) =>
							props.onChange( newValue, 'padding' )
						}
						inputProps={ { min: 0 } }
					/>
				</ToolsPanelItem>
			) }
			{ ! props.excludeButtonGap && (
				<ToolsPanelItem
					panelId={ clientId }
					hasValue={ () => !! props.value.buttonGap }
					label="Button Gap"
					onDeselect={ () => props.onReset( 'buttonGap' ) }
				>
					<UnitControl
						{ ...props }
						label="Button Gap"
						value={ props.value.buttonGap }
						onChange={ ( newValue ) =>
							props.onChange( newValue, 'buttonGap' )
						}
					/>
				</ToolsPanelItem>
			) }
		</>
	);
}

export default DimensionControl;
