import React from 'react';
/**
 * WordPress Dependencies
 */
import { useBlockEditContext } from '@wordpress/block-editor';
import {
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalBorderBoxControl as BorderBoxControl,
	__experimentalBoxControl as BoxControl,
} from '@wordpress/components';

function BorderControl( props ) {
	const { clientId } = useBlockEditContext();

	return (
		<>
			{ ! props.excludeBorder && (
				<ToolsPanelItem
					panelId={ clientId }
					hasValue={ () => !! props.borderValue }
					label="Button Border"
					onDeselect={ props.onBorderReset }
				>
					<BorderBoxControl
						enableAlpha
						colors={ props.colors }
						label="Button Border"
						onChange={ props.onBorderChange }
						value={ props.borderValue }
						{ ...props }
					/>
				</ToolsPanelItem>
			) }
			{ ! props.excludeBorderRadius && (
				<ToolsPanelItem
					panelId={ clientId }
					hasValue={ () => !! props.borderRadiusValue }
					label="Button Border Radius"
					onDeselect={ props.onBorderRadiusReset }
				>
					<BoxControl
						resetValues={ {} }
						label="Button Border Radius"
						values={ props.borderRadiusValue }
						onChange={ props.onBorderRadiusChange }
						{ ...props }
					/>
				</ToolsPanelItem>
			) }
		</>
	);
}

export default BorderControl;
